.login {
  width: 100%;
  height: 100%;
  background-image: url(/static/media/bg.8043cf2a.jpg);
  background-size: 100% 100%;
}
.login .header {
  display: flex;
  height: 64px;
  background-color: #1890ff;
}
.login .header img {
  width: 40px;
  height: 40px;
  margin: auto 15px;
}
.login .header h1 {
  color: #fff;
  font-size: 24px;
  line-height: 64px;
  font-weight: 300;
}
.login .loginBox {
  width: 300px;
  height: 238px;
  background-color: #fff;
  padding: 15px 25px;
  margin: 40px auto;
  border-radius: 5px;
}
.login .loginBox h1 {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}
.login .loginBox .login-form .login-form-button {
  width: 100%;
}
